<template>
  <a-modal title="模板" width="50%" :footer="null" :visible="visible" class="ec-draw-form">
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <ec-panel :auto="false">
          <ec-panel-item>
            <!-- <ec-panel-item-right>
              <ec-query-item label="基地单元名称">
                <a-input type="text" v-model="baseCellName"></a-input>
              </ec-query-item>
              <a-button type="primary" @click="queryList">查询</a-button>
            </ec-panel-item-right> -->
          </ec-panel-item>
        </ec-panel>
        <vxe-table
          ref="xTable"
          border
          show-overflow
          highlight-hover-row
          height="300"
          header-align="center"
          align="center"
          :data="tableData"
          @checkbox-change="selectcolumn"
        >
          <vxe-column type="checkbox" width="60" align="center" />
          <vxe-column width="60" type="seq" title="序号" align="center" />
          <vxe-column title="模板" field="opinionTemplate"></vxe-column>
          <vxe-column title="操作" align="center" #default="{ row }">
            <a-button type="primary" @click="handlerdelete(row)">删除</a-button>
          </vxe-column>
        </vxe-table>
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button type="primary" @click="handlerOK">确定</a-button>
          <a-button @click="onclose">关闭</a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </a-modal>
</template>
<script>
export default {
  name: 'BaseModel',
  data() {
    return {
      records: [],
      tableData: [],
      queryItems: {},
      baseCellName: '',
      orgTypeEk: '',
      busiYear: null,
      bizCode: null,
      visible: false,
    };
  },
  computed: {
    _apis() {
      return this.$apis.apm.dailyApply;
    },
  },
  methods: {
    // 数据初始化
    // 显示推窗
    show() {
      // this.orgTypeEk = props.orgTypeEk;
      // this.busiYear = props.bizYear;
      // this.bizCode = props.bizCode;
      this.visible = true;
    },
    queryList() {},
    // 删除模板
    handlerdelete() {
      const params = {};
      this._apis.deleteModule(params).then(() => {});
    },
    handlerOK() {
      if (!this.records.length) {
        this.$message.error('请先选择模板数据！');
        return;
      }
      console.log(this.records);
      this.onclose();
    },
    // 选中行
    selectcolumn({ records }) {
      this.records = records;
    },
    // 关闭
    onclose() {
      this.visible = false;
    },
  },
};
</script>
<style lang="less" scoped>
/deep/.ant-modal-header {
  padding: 8px 24px;
}
/deep/.ant-modal-body {
  padding: 0;
}
.ec-container .ec-container-body {
  background-color: transparent;
}
.ant-descriptions:not(:last-child) {
  margin-bottom: 15px;
}
/deep/.ant-descriptions-title {
  margin-bottom: 20px;
  color: rgba(24, 144, 255, 1);
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
}
/deep/.ec-container .ec-container-body {
  padding-top: 6px;
}
/deep/.ec-panel {
  padding-top: 2px;
  padding-bottom: 2px;
}
</style>
