<template>
  <a-modal title="设置待审人员" width="40%" :footer="null" :visible="visible">
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <ec-panel :auto="false">
          <ec-panel-item>
            <ec-panel-item-left>已经设置待审人员:'***' </ec-panel-item-left>
          </ec-panel-item>
        </ec-panel>
        <vxe-table
          ref="xTable"
          show-overflow
          height="300"
          header-align="center"
          align="center"
          keep-source
          :data="tableData"
          :checkbox-config="{ showHeader: false }"
          @checkbox-change="selectcolumn"
        >
          <vxe-column type="checkbox" width="60" align="center" />
          <vxe-column width="60" type="seq" title="序号" align="center" />
          <vxe-column title="省公司" field="baseCellName"></vxe-column>
          <vxe-column title="市公司" field="leafTypeName"></vxe-column>
        </vxe-table>
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button type="primary" @click="handlerOK">确定</a-button>
          <a-button @click="handleResetTable">重置</a-button>
          <a-button @click="onclose">关闭</a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </a-modal>
</template>
<script>
export default {
  name: 'AuditAwaitModel',
  data() {
    return {
      records: [],
      tableData: [{}, {}],
      busiYear: null,
      bizCode: null,
      visible: false,
    };
  },
  computed: {
    _apis() {
      return this.$apis.apm.dailyApply;
    },
  },
  methods: {
    // 数据初始化
    // 显示推窗
    show() {
      // this.orgTypeEk = props.orgTypeEk;
      // this.busiYear = props.bizYear;
      // this.bizCode = props.bizCode;
      this.visible = true;
    },
    queryList() {
      // if (!this.bizCode) return;
      // const params = {
      //   ...this.queryItems,
      //   baseCellName: this.baseCellName,
      //   busiYear: this.busiYear,
      // };
      // if (this.orgTypeEk === 'INDUSTRY') {
      //   params.linkInduOrgCode = this.bizCode;
      // } else {
      //   params.cityOrgCode = this.bizCode;
      // }
      // this._apis.queryBaseState(params).then(() => {});
    },
    handlerOK() {
      if (!this.records.length) {
        this.$message.error('请先选择数据！');
        return;
      }
      this.$emit('select', this.records[0]);
      this.onclose();
    },
    // 重置 .revertData(); // 还原api
    async handleResetTable() {
      const $table = this.$refs.xTable;
      await $table.revertData();
    },
    selectcolumn({ records }) {
      this.records = records;
    },
    // 关闭
    onclose() {
      this.tableData = [];
      this.visible = false;
    },
  },
};
</script>
<style lang="less" scoped>
/deep/.ant-modal-header {
  padding: 8px 24px;
}
/deep/.ant-modal-body {
  padding: 0;
}
.ec-container .ec-container-body {
  background-color: transparent;
}
.ant-descriptions:not(:last-child) {
  margin-bottom: 15px;
}
/deep/.ant-descriptions-title {
  margin-bottom: 20px;
  color: rgba(24, 144, 255, 1);
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
}
/deep/.ec-container .ec-container-body {
  padding-top: 6px;
}
/deep/.ec-panel {
  padding-top: 2px;
  padding-bottom: 2px;
}
</style>
