<template>
  <ec-container height="100%">
    <ec-container-header>
      <ec-panel-toolbar>
        <ec-panel-item-left>
          <ec-header title="交易衔接量审核" icon="onetomany" color-code="001" />
        </ec-panel-item-left>
        <ec-panel-item-right>
          <ec-query-item label="小数位数">
            <ec-select showSearch v-model="point">
              <a-select-option v-for="item in pointOptions" :key="item.id">
                {{ item.label }}
              </a-select-option>
            </ec-select>
          </ec-query-item>
          <a-divider type="vertical" />
          业务年度:{{ bizYear }}年
        </ec-panel-item-right>
      </ec-panel-toolbar>
    </ec-container-header>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-panel-item>
            <ec-query-panel :defaultFold="true" @query="handleQuery" @reset="queryReset">
              <ec-query-item label="办理状态">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="queryItems.auditState"
                  @change="finishStateChange"
                >
                  <a-select-option v-for="item in billDealTypeList" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="业务类型">
                <a-cascader
                  v-model="queryItems.apprTypeCode"
                  :options="apprTypeCodeList"
                  placeholder="请选择"
                />
              </ec-query-item>
              <ec-query-item label="操作类型">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="queryItems.operateType"
                >
                  <a-select-option v-for="item in operateTypeListForDaily" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <template #inner>
                <ec-query-item label="供方单位">
                  <SelectPicker
                    title="选择单位"
                    :lazy="false"
                    :search="false"
                    v-model="queryItems.supplyOrgCode"
                    :fetchData="getfetchData"
                    @ok="handlepickerValue($event, 'supplyOrgCode')"
                  />
                </ec-query-item>
                <ec-query-item label="供方单位类型">
                  <ec-select
                    showSearch
                    allowClear
                    mode="multiple"
                    placeholder="请选择"
                    v-model="queryItems.supplyOrgTypes"
                  >
                    <a-select-option v-for="item in orgTypeList" :key="item.orgType">
                      {{ item.orgTypeName }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="需方单位">
                  <SelectPicker
                    title="选择单位"
                    :lazy="false"
                    :search="false"
                    v-model="queryItems.demandOrgCode"
                    :fetchData="getfetchData"
                    @ok="handlepickerValue($event, 'supplyOrgCode')"
                  />
                </ec-query-item>
                <ec-query-item label="需方单位类型">
                  <ec-select
                    showSearch
                    allowClear
                    mode="multiple"
                    placeholder="请选择"
                    v-model="queryItems.demandOrgTypes"
                  >
                    <a-select-option v-for="item in orgTypeList" :key="item.orgType">
                      {{ item.orgTypeName }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="排序方式">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="queryItems.sortType"
                  >
                    <a-select-option v-for="item in billSortType" :key="item.id">
                      {{ item.label }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="是否为结转业务">
                  <ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="queryItems.isCheckBusi"
                  >
                    <a-select-option v-for="item in yesOrNoList" :key="item.id">
                      {{ item.label }}
                    </a-select-option>
                  </ec-select>
                </ec-query-item>
                <ec-query-item label="申请日期">
                  <a-range-picker v-model="applyDate" @change="applyTimeChange" />
                </ec-query-item>
                <ec-query-item label="单据号">
                  <a-input v-model="queryItems.billNo" />
                </ec-query-item>
              </template>
            </ec-query-panel>
          </ec-panel-item>
          <ec-divider />
        </ec-panel-toolbar>
        <!-- 表格工具栏 -->
        <ec-panel-item>
          <ec-panel-item-left>
            <a-button
              v-if="orgLevelEk === '1' && (auditLevel === '3' || auditLevel === '2')"
              type="primary"
              @click="handlerAudit"
              :loading="auditloading"
              >终审</a-button
            >
            <a-button
              v-if="orgLevelEk === '2' || auditLevel === '1'"
              @click="handlerSend"
              :loading="sendloading"
              >发送</a-button
            >
            <!-- <a-button @click="handlerSetAwaitAudit">设置待审</a-button> -->
          </ec-panel-item-left>
        </ec-panel-item>
        <ec-panel-body>
          <ec-table
            ref="table"
            border
            header-align="center"
            align="center"
            :auto="false"
            show-overflow
            height="100%"
            :loading="loading"
            :data="tableData"
            :pagination="pagination"
            @page-action="handlePagination"
            @checkbox-all="selectRowChange"
            @checkbox-change="selectRowChange"
          >
            <ec-table-col width="80" type="checkbox" align="center"></ec-table-col>
            <ec-table-col title="序号" width="80" type="seq" align="center"></ec-table-col>
            <ec-table-col title="办理状态" field="billStates" #default="{ row }">
              {{ getbillDealType(row.billStates) }}
            </ec-table-col>
            <ec-table-col title="单据号" field="billNo"></ec-table-col>
            <ec-table-col title="供方单位" field="supplyOrgName"></ec-table-col>
            <ec-table-col title="需方单位" field="demandOrgName"></ec-table-col>
            <ec-table-col title="业务类型" field="apprTypeCode" #default="{ row }">
              {{ getapprTypeCode(row.apprTypeCode) }}
            </ec-table-col>
            <ec-table-col title="操作类型" field="operateType" #default="{ row }">
              {{ getoperateType(row.operateType) }}
            </ec-table-col>
            <ec-table-col title="申请日期" field="applyDate" #default="{ row }">
              {{ moment(new Date(row.applyDate)).format('YYYY-MM-DD') }}
            </ec-table-col>
            <ec-table-col title="操作" #default="{ row }" align="center">
              <a-button type="link" @click="handlerOpenDetails(row)">详细</a-button>
            </ec-table-col>
          </ec-table>
        </ec-panel-body>
      </ec-panel>
      <AddModel
        ref="addModalRef"
        :operateTypeListForDaily="operateTypeListForDaily"
        :newOrOldLeafList="newOrOldLeafList"
        :orgTreeData="orgTreeData"
        :orgLevelEk="orgLevelEk"
        :auditLevel="auditLevel"
        :point="point"
        @finish="handleQuery"
      />
      <AwaitModel ref="awaitmodelRef" />
    </ec-container-body>
  </ec-container>
</template>

<script>
import { SelectPicker } from '@euler-material/ecm-ui';
import { treeFormatList, cloneDeep } from '@euler-material/ecm-utils';
import AddModel from './AddModel.vue';
import AwaitModel from './AwaitModel.vue';
import moment from 'moment';

export default {
  name: 'dailyQuery',
  components: {
    AddModel,
    AwaitModel,
    SelectPicker,
  },
  data() {
    return {
      // 登陆级别
      auditLevel: null,
      stopParam: null,

      // 初始基本类型
      applyDate: null,
      weightunit: '50',
      point: 2,

      loading: true,
      auditloading: false,
      sendloading: false,
      tableData: [],
      records: [],
      queryItems: {
        pageNumber: 1,
        pageSize: 10,
      },
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      // options
      operateTypeList: [],
      weightUnitList: [],
      leafTypeCodeList: [],
      leafFormCodeList: [],
      transStatesList: [],
      // 小数选项
      pointOptions: [
        { id: 2, label: '2位' },
        { id: 4, label: '4位' },
      ],
      // 办理状态
      billDealTypeList: [],
      // 全部办理状态
      allbillDealTypeList: [
        { id: '0', label: '删除申请' },
        { id: '1', label: '确认申请' },
        { id: '2', label: '经办人初审' },
        { id: '3', label: '处长审核' },
        { id: '4', label: '副总经理审核' },
        { id: '5', label: '总经理审核' },
        { id: '6', label: '经办人发送' },
        { id: '7', label: '省公司发送申请' },
      ],
      // 控制状态 的状态
      finishStateListOptions: [
        { id: '', label: '全部', usedState: '1' },
        { id: '0', label: '进行中', usedState: '1' },
        { id: '1', label: '完成', usedState: '0' },
        { id: '2', label: '废止', usedState: '0' },
      ],
      // 排序方式
      billSortType: [
        { id: '1', label: '申请日期', usedState: '1' },
        { id: '2', label: '业务类型', usedState: '0' },
        { id: '3', label: '需方单位', usedState: '0' },
        { id: '4', label: '供方单位', usedState: '0' },
        { id: '5', label: '处理类型', usedState: '0' },
        { id: '6', label: '单据号', usedState: '0' },
      ],
      // 发送状态
      transStatesEnumList: [
        { id: '0', label: '未发送', auditState: '0' },
        { id: '1', label: '已发送', auditState: '1' },
      ],
      // 操作类型
      operateTypeListForDaily: [
        { id: '1', label: '增量', usedState: '1' },
        { id: '2', label: '减量', usedState: '0' },
        { id: '3', label: '解除', usedState: '0' },
      ],
      // 业务类型
      apprTypeCodeListed: [], // 备份
      apprTypeCodeList: [],
      // 烟叶
      newOrOldLeafList: [
        { id: '1', label: '新烟', sendState: '0' },
        { id: '2', label: '陈烟', sendState: '1' },
      ],
      // 单位查询范围
      orgCidSearchRangeList: [
        { id: 'BJ', label: '本级', isSelected: true },
        { id: 'BJJXJ', label: '本级及下级' },
      ],
      // 是否结转
      yesOrNoList: [
        { id: '1', label: '是' },
        { id: '0', label: '否' },
      ],
      // 全部流转状态
      billStatesList: [],
      // 组织树Provider
      orgTreeData: [],
      // 单位类型
      orgTypeList: [
        { orgType: 'TOBACCO', orgTypeName: '商业' },
        { orgType: 'INDUSTRY', orgTypeName: '工业' },
        { orgType: 'REDRIED', orgTypeName: '复烤企业' },
        { orgType: 'PIECE', orgTypeName: '薄片加工企业' },
        { orgType: 'CUTTER', orgTypeName: '烟丝企业' },
        { orgType: 'EXPORT', orgTypeName: '进出口企业（到口岸）' },
      ],
    };
  },
  computed: {
    _apis() {
      return this.$apis.apm.dailyAudit;
    },
    _apply() {
      return this.$apis.centre.apply;
    },
    _dailyQuery() {
      return this.$apis.apm.dailyQuery;
    },
    userInfo() {
      return this.$store.getters.getAppConfig;
    },
    orgLevelEk() {
      return this.$store.getters.getAppConfig.orgLevelEk;
    },
    bizYear() {
      return this.$store.getters.getAppConfig?.yearValueMap?.DAILY_YEAR;
    },
    // 全部的流转状态
    allBillStatesList() {
      return this.userInfo.orgLevelEk === '1'
        ? [
            { id: '0', label: '删除中', usedState: '1' },
            { id: '1', label: '对方单位确认中', usedState: '0' },
            { id: '2', label: '省公司确认中', usedState: '0' },
            { id: '3', label: '经办人初审中', usedState: '0' },
            { id: '4', label: '处长审核中', usedState: '0' },
            { id: '5', label: '副总经理审核中', usedState: '0' },
            { id: '6', label: '总经理审核中', usedState: '0' },
            { id: 'gjj', label: '总公司审核同意', usedState: '1' },
            { id: 'prov', label: '省公司审核同意', usedState: '1' },
            { id: '9', label: '已删除', usedState: '0' },
            { id: '10', label: '废止', usedState: '0' },
          ]
        : [
            { id: '0', label: '删除中', usedState: '1' },
            { id: '1', label: '对方单位确认中', usedState: '0' },
            { id: '2', label: '省公司确认中', usedState: '0' },
            { id: '3,4,5,6', label: '总公司审核中', usedState: '0' },
            { id: 'gjj', label: '总公司审核同意', usedState: '1' },
            { id: 'prov', label: '省公司审核同意', usedState: '1' },
            { id: '9', label: '已删除', usedState: '0' },
            { id: '10', label: '废止', usedState: '0' },
          ];
    },
  },
  created() {
    this.init();
  },
  methods: {
    moment,
    // 分页查询
    async getTableData() {
      if (!this.bizYear) return;
      try {
        const parmas = {
          ...this.queryItems,
          busiYear: this.bizYear,
        };
        const res = await this._apis.QueryList(parmas);
        this.tableData = res.rows;
        this.pagination.total = res.totalRows;
        this.loading = false;
      } catch (err) {
        console.log(err);
        this.tableData = [];
        this.loading = false;
      }
    },
    // 分页
    handlePagination(pageNumber, pageSize) {
      this.queryItems.pageNumber = pageNumber;
      this.queryItems.pageSize = pageSize;
      this.handleQuery();
    },
    // 按钮-搜索
    handleQuery() {
      this.getTableData();
    },
    // 按钮-重置
    queryReset() {
      this.applyDate = null;
      this.queryItems.pageNumber = 1;
      this.queryItems.pageSize = 10;
      this.queryItems.supplyOrgTypes = null;
      this.queryItems.demandOrgTypes = null;
      this.queryItems.busiYear = null;
      this.queryItems.supplyOrgCode = null;
      this.queryItems.demandOrgCode = null;
      this.queryItems.operateType = null;
      this.queryItems.isCheckBusi = null;
      this.queryItems.beginApplyDate = null;
      this.queryItems.endApplyDate = null;
      this.queryItems.apprTypeCode = null;
      this.queryItems.billNo = null;
      this.queryItems.auditState = null;
      this.queryItems.sortType = null;
    },
    // 按钮-终审
    handlerAudit(tipMsg = '终审') {
      if (!this.records.length) {
        this.$message.error('请选择要终审的数据！');
        return;
      }
      this.$confirm({
        title: '操作提示?',
        content: () => (
          <div style="color:red;">是否确认终审？，确认请点击'确认'按钮，取消点击'取消'按钮</div>
        ),
        onOk: () => {
          this.auditloading = true;
          const ids = this.records?.map((item) => item.asDailyDealMasId);
          const params = {
            busiYear: this.bizYear,
            asDailyDealMasIds: ids,
          };
          this._apis
            .lastAudit(params)
            .then(({ result }) => {
              if (result) {
                this.$message.success(`${tipMsg}操作成功`);
                this.handleQuery();
              } else {
                this.$message.error(`${tipMsg}操作失败`);
              }
            })
            .finally(() => {
              this.auditloading = false;
            });
        },
      });
    },
    // 按钮-发送
    handlerSend() {
      if (!this.records.length) {
        this.$message.error('请选择要发送的数据！');
        return;
      }
      const isok = this.records?.every(
        (item) => item.billStates === '7' || item.billStates === '11',
      );
      if (!isok) {
        this.$message.error('当前状态不允许发送');
        return;
      }
      this.$confirm({
        title: '操作提示?',
        content: () => (
          <div style="color:red;">是否发送选中记录？，确认请点击'确认'按钮，取消点击'取消'按钮</div>
        ),
        onOk: () => {
          const ids = this.records?.map((item) => item.asDailyDealMasId);
          const tipMsg = '发送';
          this.sendloading = true;
          const params = {
            busiYear: this.bizYear,
            asDailyDealMasIds: ids,
          };
          this._apis
            .updateStatusAll(params)
            .then(({ result }) => {
              if (result) {
                this.$message.success(`${tipMsg}操作成功`);
                this.handleQuery();
              } else {
                this.$message.error(`${tipMsg}操作失败`);
              }
            })
            .finally(() => {
              this.sendloading = false;
            });
        },
      });
    },
    // 设置待审
    handlerSetAwaitAudit() {
      this.$refs.awaitmodelRef.show();
    },
    // 按钮-详细
    handlerOpenDetails(row) {
      const params = { asDailyDealMasId: row.asDailyDealMasId, busiYear: this.bizYear };
      this._dailyQuery.queryDetails(params).then((res) => {
        const apprType = this.apprTypeCodeListed?.find(
          (item) => item.contTypeCode === res.apprTypeCode,
        );
        const bprops = {
          ...res,
          apprTypeName: apprType?.contTypeName,
          weightunit: this.weightunit,
          busiYear: this.bizYear,
          billStates: row.billStates,
        };
        this.$refs.addModalRef.show(bprops);
      });
    },
    // 状态集合
    finishStateChange(val) {
      const isgjj = this.userInfo.orgLevelEk === '1'; // 国家局
      // 选择进行中
      if (val === '0') {
        const isgjjfilter = isgjj
          ? (item) =>
              item.id === '0' ||
              item.id === '1' ||
              item.id === '2' ||
              item.id === '3' ||
              item.id === '4' ||
              item.id === '5' ||
              item.id === '6'
          : (item) =>
              item.id === '0' || item.id === '1' || item.id === '2' || item.id === '3,4,5,6';
        this.billStatesList = this.allBillStatesList.filter(isgjjfilter);
      } else if (val === '1') {
        // 选择完成
        const getfilterItem = (item) => item.id === 'gjj' || item.id === 'prov' || item.id === '9';
        this.billStatesList = this.allBillStatesList.filter(getfilterItem);
      } else if (val === '2') {
        // 选择终止
        this.billStatesList = this.allBillStatesList.filter((item) => item.id === '10');
      } else {
        this.billStatesList = this.allBillStatesList.map((item) => item);
      }
    },
    // 获取枚举
    getEnumerationdata() {
      this.billStatesList = [...this.finishStateListOptions];
      this.querygetBusinessType();
      this.getbillDealTypeOptionsAuditLevel();
      this.$apis.enum.index
        .getEnumType({
          enumTypeCodes: ['OPERATE_TYPE', 'WEIGHT_UNIT', 'SEND_STATE', 'TRANS_STATES'],
        })
        .then((res) => {
          if (res) {
            this.operateTypeList = res.enumMap.OPERATE_TYPE;
            this.weightUnitList = res.enumMap.WEIGHT_UNIT;
            // this.leafTypeCodeList = res.enumMap.LEAF_TYPE_CD;
            // this.leafFormCodeList = res.enumMap.LEAF_FORM_CD;
            this.transStatesList = res.enumMap.TRANS_STATES;
          }
        });
    },
    // 获取业务类型
    querygetBusinessType() {
      this.$apis.common.index.queryContType({ contTypeCode: '2' }).then(({ rows }) => {
        this.apprTypeCodeListed = rows;
        this.apprTypeCodeList = this.getTree(
          rows,
          'higherContTypeCode',
          'contTypeCode',
          'contTypeName',
          'children',
          '0',
        );
      });
    },
    // 获取办理状态 用户级别 独立接口Audit模块
    getbillDealTypeOptionsAuditLevel() {
      this._apis.QueryInit({}).then((res) => {
        this.auditLevel = res.auditLevel;
        this.stopParam = res.stopParam;
        const obj = res.billDealTypeList[0];
        const providerLabelArray = Object.entries(obj);
        const list = [];
        for (let k = 0; k < providerLabelArray.length; k++) {
          const node = providerLabelArray[k];
          list.push({ id: node[0], label: node[1] });
        }
        this.billDealTypeList = list;
      });
    },
    // 获取业务类型
    getTree(rows, pid, id, title, children, root) {
      // 递归 161202 次 5ms左右时间
      console.time();
      const list2tree1 = (list, topid) => {
        return list.filter((item) => {
          if (item[pid] === topid) {
            (item.value = item[id])((item.label = item[title]))(
              (item[children] = list2tree1(list, item[id])),
            );
            return true;
          }
          return false;
        });
      };
      const tree = list2tree1(rows, root);
      console.timeEnd();
      return tree;
    },
    // 选中行
    selectRowChange({ records }) {
      this.records = records;
    },
    // 初始化
    async init() {
      this.getEnumerationdata();
      this.treeGetlist(this.bizYear, treeFormatList, this._apply);
    },
    // utils
    // 获取组织树-数据
    async treeGetlist(yearValue, utils, api) {
      const treeParams = {
        bizYear: yearValue,
        bizCode: 'BA20111000001000000000000',
        orgTypeEk: 'EXPORT,INDUSTRY,TOBACCO',
        orgLevelEk: '3',
        paramMap: {
          isCorporation: '1',
        },
      };
      const fields = {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      };
      const rows = JSON.parse(localStorage.getItem('apprlist')) || [];
      if (rows.length) {
        this.orgTreeData = utils(rows, fields);
      } else {
        const response = await api.getreeDate(treeParams);
        this.orgTreeData = utils(response?.rows, fields);
      }
    },
    // 渲染组织树
    getfetchData() {
      return new Promise((resolve) => {
        const list = cloneDeep(this.orgTreeData);
        resolve(list);
      });
    },
    // choose办理状态类型
    getbillDealType(id) {
      return this.allbillDealTypeList?.find((item) => item.id === id)?.label || '';
    },
    // 操作类型
    getoperateType(id) {
      return this.operateTypeListForDaily?.find((item) => item.id === id)?.label || '';
    },
    // 业务类型
    getapprTypeCode(id) {
      return this.apprTypeCodeListed?.find((item) => item.contTypeCode === id)?.contTypeName || '';
    },
    // 单元格style
    cellStyle({ $columnIndex }) {
      if ($columnIndex === 2 || $columnIndex === 1) {
        return { textAlign: 'left' };
      }
    },
    // 时间匹配
    applyTimeChange(time) {
      this.queryItems.beginApplyDate = time[0].format('YYYY-MM-DD HH:mm:ss') || '';
      this.queryItems.endApplyDate = time[0].format('YYYY-MM-DD HH:mm:ss') || '';
    },
  },
};
</script>

<style lang="less" scoped>
/deep/.ec-query-item.ec-query-item-inline .ec-query-item__control {
  min-width: 80px;
}
</style>
