<template>
  <a-modal title="意见理由弹出窗" width="50%" :footer="null" :visible="visible">
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <ec-panel :auto="false">
          <ec-panel-item>
            <ec-panel-item-left>{{ btnName }}理由 </ec-panel-item-left>
          </ec-panel-item>
          <ec-panel-item>
            <template>
              <a-textarea v-model="applyReasion" :rows="20" />
            </template>
          </ec-panel-item>
        </ec-panel>
        <SelectModule ref="selectmoduleRef" />
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <!-- 国家局可见 -->
          <a-button v-if="orgLevelEk === '2'" type="primary" @click="selectModule"
            >选取模板</a-button
          >
          <a-button v-if="orgLevelEk === '2'" type="primary" @click="saveCurentModule"
            >保存模板</a-button
          >
          <a-button type="primary" @click="handlerOK">{{ btnName }}</a-button>
          <a-button @click="onclose">关闭</a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </a-modal>
</template>
<script>
import SelectModule from './SelectModule.vue';
export default {
  name: 'AuditIdeaModel',
  components: { SelectModule },
  props: {
    orgLevelEk: {
      type: String,
      default: '',
    },
    btnName: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      applyReasion: '',
      visible: false,
    };
  },
  methods: {
    // 数据初始化
    // 显示推窗
    show() {
      this.visible = true;
    },
    // 确定
    handlerOK() {
      const { applyReasion, btnName } = this;
      if (!applyReasion) {
        this.$message.error('意见不能为空');
        return;
      }
      this.$emit('select', { applyReasion, btnName });
      this.onclose();
    },
    // 选择模板
    selectModule() {
      // const params = {
      //   type: this.btnName === '同意' ? 1 : 2,
      // };
      // this._apis.selectmodule(params).then((res) => {
      //   debugger;
      // });
      this.$refs.selectmoduleRef.show();
    },
    // 保存模板
    saveCurentModule() {
      const { applyReasion: auditOpinionStr, btnName } = this;
      if (!auditOpinionStr) {
        this.$message.error(`${btnName}原因不能为空`);
        return;
      }
      const params = {
        auditOpinionStr,
        type: btnName === '同意' ? 1 : 2,
      };
      this._apis.saveModule(params).then(() => {});
    },
    // 关闭
    onclose() {
      this.visible = false;
    },
  },
};
</script>
<style lang="less" scoped>
/deep/.ant-modal-header {
  padding: 8px 24px;
}
/deep/.ant-modal-body {
  padding: 0;
}
.ec-container .ec-container-body {
  background-color: transparent;
}
.ant-descriptions:not(:last-child) {
  margin-bottom: 15px;
}
/deep/.ant-descriptions-title {
  margin-bottom: 20px;
  color: rgba(24, 144, 255, 1);
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
}
/deep/.ec-container .ec-container-body {
  padding-top: 6px;
}
/deep/.ec-panel {
  padding-top: 2px;
  padding-bottom: 2px;
}
</style>
