<template>
  <a-modal title="库存详细信息" width="50%" :footer="null" :visible="visible" class="ec-draw-form">
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <ec-panel :auto="false">
          <ec-panel-item>
            <ec-panel-item-right>
              <div class="header-right">
                <p>
                  <span>过去三年薄片产出率:{{ '10%' }}</span>
