            </ec-panel-item-right>
          </ec-panel-item>
        </ec-panel>
        <vxe-table
          ref="xTable"
          border
          show-overflow
          highlight-hover-row
          height="300"
          header-align="center"
          align="center"
          :data="tableData"
        >
          <vxe-column width="60" type="seq" title="序号" align="center" />

          <vxe-column title="烟叶类型" field="baseCellName"></vxe-column>
          <vxe-column title="烟叶形态" field="leafTypeName"></vxe-column>
          <vxe-column title="调出计划" field="limitWeight"></vxe-column>
          <vxe-column title="调入计划" field="limitWeight"></vxe-column>
          <vxe-colgroup field="b" title="库存量">
