export default {
  name: 'AuditWarehouseStorageModel',
  props: {
    weightunit: {
      type: String,
      default: '50',
    },
    bizYear: {
      type: Number,
      default: 2022,
    },
  },
  data() {
    return {
      records: [],
      tableData: [{}, {}],
      busiYear: null,
      bizCode: null,
      visible: false,
    };
  },
  methods: {
    // 数据初始化
    // 显示推窗
    show() {
      // this.orgTypeEk = props.orgTypeEk;
      // this.bizCode = props.bizCode;
      this.visible = true;
    },
    queryList() {},
    // 关闭
    onclose() {
      this.visible = false;
    },
  },
};
</script>
