<template>
  <a-modal title="审核意见详细信息窗口" width="48%" :footer="null" :visible="visible">
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <ec-panel :auto="false">
          <ec-panel-item>
            <ec-form ref="modelAddForm" :model="ruleForm" :rules="rules">
              <a-row type="flex" :gutter="12">
                <a-col :span="12">
                  <a-form-model-item label="操作人" prop="auditorName">
                    <a-input type="text" v-model="ruleForm.auditorName"></a-input>
                  </a-form-model-item>
                </a-col>
                <a-col :span="12">
                  <a-form-model-item label="操作日期" prop="auditTime">
                    <a-input type="text" v-model="ruleForm.auditTime"></a-input>
                  </a-form-model-item>
                </a-col>
                <a-col :span="24">
                  <a-form-model-item label="意见" prop="auditOpinion">
                    <a-textarea v-model="ruleForm.auditOpinion" :rows="10" />
                  </a-form-model-item>
                </a-col>
              </a-row>
            </ec-form>
          </ec-panel-item>
        </ec-panel>
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button @click="onclose">关闭</a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </a-modal>
</template>
<script>
export default {
  name: 'AuditIdeaModel',
  data() {
    return {
      visible: false,
      ruleForm: {},
      rules: {},
    };
  },
  methods: {
    // 数据初始化
    // 显示推窗
    show(props) {
      Object.assign(this.ruleForm, props);
      this.visible = true;
    },
    // 关闭
    onclose() {
      this.visible = false;
    },
  },
};
</script>
<style lang="less" scoped>
