  margin-bottom: 20px;
  color: rgba(24, 144, 255, 1);
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
}
/deep/.ec-container .ec-container-body {
  padding-top: 6px;
}
/deep/.ec-panel {
