<template>
  <ec-drawer title="详细" :visible="visible" size="large" @close="onclose">
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <div class="header-title">
          <div><span class="middle-title">主信息</span></div>
          <div>
            <span class="middle-title"
              >业务年度：{{ busiYear }} 计量单位：{{ weightunit }} 是否为转结业务：否</span
            >
          </div>
        </div>
        <ec-form ref="ruleForm" :model="ruleForm" :rules="rules">
          <a-row type="flex">
            <a-col :span="8">
              <a-form-model-item label="供方单位">
                <SelectPicker
                  title="选择单位"
                  :lazy="false"
                  :search="false"
                  v-model="ruleForm.supplyOrgCode"
                  :fetchData="getfetchData"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="需方单位">
                <SelectPicker
                  title="选择单位"
                  :lazy="false"
                  :search="false"
                  v-model="ruleForm.demandOrgCode"
                  :fetchData="getfetchData"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="申请日期">
                <a-date-picker
                  v-model="ruleForm.applyDate"
                  show-time
                  format="YYYY-MM-DD"
                  value-format="YYYY-MM-DD"
                />
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="操作类型">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="ruleForm.operateType"
                >
                  <a-select-option v-for="item in operateTypeListForDaily" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="业务类型">
                <a-input v-model="ruleForm.apprTypeName" autocomplete="off" />
              </a-form-model-item>
            </a-col>
            <a-col :span="8">
              <a-form-model-item label="陈烟/原烟">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="ruleForm.operateType"
                >
                  <a-select-option v-for="item in newOrOldLeafList" :key="item.id">
                    {{ item.label }}
                  </a-select-option>
                </ec-select>
              </a-form-model-item>
            </a-col>
          </a-row>
        </ec-form>
        <a-divider />
        <a-row style="margin: 0 0 6px 0">
          <a-col :span="12"><span class="middle-title">明细信息</span></a-col>
          <a-col :span="12"><span class="middle-title right">申请原因</span></a-col>
        </a-row>
        <a-row>
          <a-col :span="12">
            <vxe-table
              border
              show-footer
              show-overflow
              header-align="center"
              align="center"
              highlight-hover-row
              ref="xTable"
              height="220"
              class="editable-footer"
              :footer-method="footerMethod"
              :data="pcTAsDailyDealDetlDTOS"
            >
              <vxe-column width="60" type="seq" title="序号" align="center" />
              <vxe-column title="烟叶类型" field="leafTypeName"></vxe-column>
              <vxe-column title="烟叶形态" field="leafFormName"></vxe-column>
              <vxe-column title="衔接量" field="apprWeight" #default="{ row }">
                {{ getPointapprWeight(row.apprWeight, point) }}</vxe-column
              ></vxe-table
            >
          </a-col>
          <a-col :span="11" :push="1">
            <a-textarea
              style="border-radius: 16px"
              v-model="applyReasion"
              placeholder=" "
              :rows="8"
            />
          </a-col>
        </a-row>
        <a-row style="margin: 0 0 6px 0">
          <a-col :span="12"><span class="middle-title">审核记录</span></a-col>
        </a-row>
        <vxe-table
          ref="xTable"
          border
          height="240"
          header-align="center"
          align="center"
          show-overflow
          highlight-hover-row
          class="editable-footer"
          :data="pcTAsDailyDealAuditOpinionDTOS"
        >
          <vxe-column title="序号" width="60" type="seq" align="center" />
          <vxe-column title="操作人" field="auditorName"></vxe-column>
          <vxe-column title="意见" field="auditOpinion"></vxe-column>
          <vxe-column title="操作日期" field="auditTime"></vxe-column>
          <vxe-column title="操作" #default="{ row }">
            <a-button type="link" @click="handlerOpenAuditDetails(row)">详细</a-button></vxe-column
          ></vxe-table
        >
        <a-row style="margin: 0 0 6px 0">
          <a-col :span="12"><span class="middle-title">申请凭据附件</span></a-col>
        </a-row>
        <a-space>
          附件:&nbsp;
          <a-button
            v-for="file in files"
            type="link"
            :key="file.asAttachFileId"
            @click="handleFileDetails(file)"
            >{{ file.fileName }}</a-button
          >
        </a-space>
        <AuditDetails ref="auditdetailsRef" />
        <TogetherDo ref="togetherdoRef" />
        <IdeaModel ref="ideaRef" :btnName="btnName" :orgLevelEk="orgLevelEk" @select="ideaEmits" />
        <WarehouseStorage ref="warehousestorageRef" :bizYear="busiYear" :weightunit="weightunit" />
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button @click="handlerTogetherDO">协办</a-button>
          <a-button
            type="primary"
            v-if="orgLevelEk === '1' && auditLevel === '1'"
            @click="handlerOpenwarehouseStorage"
            >库存量</a-button
          >
          <a-button
            type="primary"
            v-if="orgLevelEk === '1' && lastAuditIn.includes(auditLevel)"
            @click="handlerAudit"
            >终审</a-button
          >
          <a-button
            type="primary"
            v-if="
              (orgLevelEk === '1' && auditLevel === '1' && billStates !== '7') ||
              okandbackbtnisIN.includes(auditLevel) ||
              okandbackbtnisINbillStates.includes(billStates)
            "
            @click="handlerUpdateStatus('同意')"
            >同意</a-button
          >
          <a-button
            type="primary"
            v-if="
              (orgLevelEk === '1' && auditLevel === '1' && billStates !== '7') ||
              okandbackbtnisIN.includes(auditLevel) ||
              okandbackbtnisINbillStates.includes(billStates)
            "
            @click="handlerUpdateStatus('退回')"
            >退回</a-button
          >
          <a-button v-if="orgLevelEk !== '1' && billStates === '0'" @click="handlerDelete"
            >删除</a-button
          >
          <a-button
            v-if="
              (orgLevelEk === '1' && auditLevel === '1' && billStates === '7') ||
              (orgLevelEk !== '1' && billStates === '11')
            "
            @click="handlerAband"
            >废止</a-button
          >
          <a-button @click="onclose">关闭</a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </ec-drawer>
</template>
<script>
import moment from 'moment';
import { SelectPicker } from '@euler-material/ecm-ui';
import { cloneDeep } from '@euler-material/ecm-utils';
import TogetherDo from './TogetherDoModel.vue';
import IdeaModel from './IdeaModel.vue';
import WarehouseStorage from './WarehouseStorageModel.vue';
import AuditDetails from './AuditDetails.vue';
import { DownloadA } from '@/utils/utils';

export default {
  name: 'AuditAddModel',
  components: { SelectPicker, TogetherDo, WarehouseStorage, IdeaModel, AuditDetails },
  props: {
    point: {
      type: Number,
      default: 2,
    },
    orgLevelEk: {
      type: String,
      default: '',
    },
    auditLevel: {
      type: String,
      default: '',
    },
    operateTypeListForDaily: {
      type: Array,
      default: () => [],
    },
    newOrOldLeafList: {
      type: Array,
      default: () => [],
    },
    orgTreeData: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      okandbackbtnisIN: ['2', '3', '4'],
      okandbackbtnisINbillStates: ['2', '1'],
      lastAuditIn: ['2', '3'],
      billStates: null,
      busiYear: null,
      btnName: null,
      asDailyDealMasId: null,
      weightunit: null,
      applyReasion: null,
      weightUnitName: '50',
      files: [],
      pcTAsDailyDealDetlDTOS: [],
      pcTAsDailyDealAuditOpinionDTOS: [],
      ruleForm: {},
      rules: {},
      visible: false,
    };
  },
  computed: {
    _apis() {
      return this.$apis.apm.dailyAudit;
    },
  },
  methods: {
    moment,
    // 数据初始化
    // 显示推窗
    show(vprops) {
      this.files = vprops.files;
      this.billStates = vprops.billStates;
      this.asDailyDealMasId = vprops.asDailyDealMasId;

      this.busiYear = vprops.busiYear;
      this.weightunit = vprops.weightunit;
      this.applyReasion = vprops.applyReasion;
      this.ruleForm.applyDate = moment(new Date(vprops.applyDate));
      this.ruleForm.demandOrgCode = vprops.demandOrgCode;
      this.ruleForm.supplyOrgCode = vprops.supplyOrgCode;
      this.ruleForm.operateType = vprops.operateType;
      this.ruleForm.newOrOldLeaf = vprops.newOrOldLeaf;
      this.ruleForm.apprTypeName = vprops.apprTypeName;
      this.pcTAsDailyDealDetlDTOS = vprops.pcTAsDailyDealDetlDTOS;
      this.pcTAsDailyDealAuditOpinionDTOS = vprops.pcTAsDailyDealAuditOpinionDTOS;
      this.visible = true;
    },
    // 协办
    handlerTogetherDO() {
      this.$refs.togetherdoRef.show();
    },
    // 仓库存储信息
    handlerOpenwarehouseStorage() {
      // const params = {
      //   bizYear: this.busiYear,
      //   id: '***',
      // };
      // this._apis.queryWarehouseDetails(params).then((res) => {
      //   debugger;
      // });
      this.$refs.warehousestorageRef.show();
    },
    // 同意or退回
    handlerUpdateStatus(title) {
      if (this.auditLevel === '1' || (title === '退回' && this.orgLevelEk !== '1')) {
        this.$refs.ideaRef.show();
      } else {
        this.handlerSubmit(title, title);
      }
    },
    // 删除
    handlerDelete() {
      this.handlerSubmit('删除', '删除');
    },
    // 提交
    handlerSubmit(auditOpinionStr, tipMsg) {
      if (!auditOpinionStr) {
        this.$message.error(`${tipMsg}原因不能为空！`);
      } else if (auditOpinionStr.length > 300) {
        this.$message.error('输入内容不能超过三百个字符');
      } else {
        const confirmObj = {
          title: '操作提示?',
          content: () => (
            <div style="color:red;">
              是否{tipMsg}选中记录？，确认请点击'确认'按钮，取消点击'取消'按钮
            </div>
          ),
          onOk: () => {
            const params = {
              busiYear: this.busiYear,
              auditOpinion: auditOpinionStr,
              asDailyDealMasId: this.asDailyDealMasId,
            };
            $ec.showLoading();
            const apiMe = tipMsg === '退回' ? 'updateStatusExit' : 'updateStatus';
            this._apis[apiMe](params)
              .then(({ result }) => {
                if (result) {
                  this.$message.success(`${tipMsg}操作成功`);
                  this.onclose();
                  this.$emit('finish');
                } else {
                  this.$message.error(`${tipMsg}操作失败`);
                }
              })
              .finally(() => {
                $ec.hideLoading();
              });
          },
        };
        this.$confirm(confirmObj);
      }
    },
    // 来自意见框
    ideaEmits(data) {
      const { applyReasion, btnName } = data;
      this.handlerSubmit(applyReasion, btnName);
    },
    // 终审
    handlerAudit(tipMsg = '终审') {
      this.$confirm({
        title: '操作提示?',
        content: () => (
          <div style="color:red;">是否确认终审？，确认请点击'确认'按钮，取消点击'取消'按钮</div>
        ),
        onOk: () => {
          // this.auditloading = true;
          const params = {
            busiYear: this.busiYear,
            asDailyDealMasIds: [this.asDailyDealMasId],
          };
          this._apis
            .lastAudit(params)
            .then(({ result }) => {
              if (result) {
                this.$message.success(`${tipMsg}操作成功`);
                this.onclose();
                this.$emit('finish');
              } else {
                this.$message.error(`${tipMsg}操作失败`);
              }
            })
            .finally(() => {
              // this.auditloading = false;
            });
        },
      });
    },
    // 废止
    handlerAband() {
      const tipMsg = '废止';
      const confirmObj = {
        title: '操作提示?',
        content: () => (
          <div style="color:red;">
            是否{tipMsg}选中记录？，确认请点击'确认'按钮，取消点击'取消'按钮
          </div>
        ),
        onOk: () => {
          const params = {
            busiYear: this.busiYear,
            // auditOpinion: auditOpinionStr,
            asDailyDealMasId: this.asDailyDealMasId,
          };
          $ec.showLoading();
          const apiMe = 'Aband';
          this._apis[apiMe](params)
            .then(({ result }) => {
              if (result) {
                this.$message.success(`${tipMsg}操作成功`);
                this.onclose();
                this.$emit('finish');
              } else {
                this.$message.error(`${tipMsg}操作失败`);
              }
            })
            .finally(() => {
              $ec.hideLoading();
            });
        },
      };
      this.$confirm(confirmObj);
    },
    // 审核详细
    handlerOpenAuditDetails(row) {
      this.$refs.auditdetailsRef.show(row);
    },
    // 下载文件
    async handleFileDetails({ asAttachFileId }) {
      try {
        const res = await this._apis.download({ asAttachFileId });
        DownloadA(res.url, res.fileName);
      } catch (error) {
        console.log(error);
      }
    },
    // utils
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        count += Number(item[field]);
      });
      return count;
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 2) {
            return '合计';
          }
          if (['finalWeight', 'supplyWeight', 'demandWeight'].includes(column.property)) {
            return this.sumNum(data, column.property);
          }
          return null;
        }),
      ];
    },
    // 单位换算
    handlerWeightcalculate(num, unit) {
      return num / unit;
    },
    // 渲染组织树
    getfetchData() {
      return new Promise((resolve) => {
        const list = cloneDeep(this.orgTreeData);
        resolve(list);
      });
    },
    getPointapprWeight(str, num) {
      return Number(str).toFixed(num);
    },
    // 关闭
    onclose() {
      this.visible = false;
    },
  },
};
</script>
<style lang="less" scoped>
/deep/.ant-modal-header {
  padding: 8px 24px;
}
/deep/.ant-modal-body {
  padding: 0;
}
.ec-container .ec-container-body {
  background-color: transparent;
}
.ant-descriptions:not(:last-child) {
  margin-bottom: 15px;
}
/deep/.ant-descriptions-title {
  margin-bottom: 20px;
  color: rgba(24, 144, 255, 1);
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
}
.vxetable-title {
  margin-bottom: 10px;
  color: rgba(24, 144, 255, 1);
  font-weight: bold;
  font-size: 16px;
  line-height: 2.4;
}
.header-title {
  margin-bottom: 8px;
  display: flex;
  justify-content: space-between;
}
.middle-title {
  color: blue;
}
.right {
  margin-left: 56px;
}
</style>
